import React from 'react';

const renderSalary = (from, to, currency) => {
  if (from !== 0 && to !== 0) {
    return (
      <span>
        /
        {' '}
        {from}
        -
        {to}
        {' '}
        {currency}
      </span>
    );
  }

  if (from !== 0 && to === 0) {
    return (
      <span>
        /
        {' '}
        
        {' '}
        {from}
        {' '}
        {currency}
      </span>
    );
  }

  if (from !== 0 && to === 0) {
    return (
      <span>
        /
        {' '}
        {to}
        {' '}
        {currency}
      </span>
    );
  }

  return (<span>/  </span>);
};

export default renderSalary;
